.App {
	max-width: 860px;
	margin: 40px auto;
}

button {
	padding: 6px 12px;
	border: 2px solid #fff;
	border-radius: 4px;
	background: none;
	color: #fff;
	font-weight: bold;
	font-size: 1em;
	cursor: pointer;
}

button:hover {
	background: #c23866;
	color: #fff;
}

.card-grid {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-gap: 20px;
	margin-top: 40px;
}
